import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import LandingPageLayout from '../../components/landing-page/landing-page-layout/landing-page-layout'
import MailchimpEmailCapture from '../../components/mailchimp-email-capture/mailchimp-email-capture'
import LandingPageHeader from '../../components/layout/landing-page-header/landing-page-header'
import Container from '@material-ui/core/Container'
import Box from '@material-ui/core/Box'
import Card from '@material-ui/core/Card'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import Hidden from '@material-ui/core/Hidden'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import CardContent from '@material-ui/core/CardContent'
import Grid from '@material-ui/core/Grid'
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos'
import Img from 'gatsby-image'
import lastenradLandshutHero from '../../images/landing-page/lastenrad-landshut-hero.png'
import lastenradLandshutBackground from '../../images/landing-page/lastenrad-landshut-background.png'
import { Helmet } from 'react-helmet'

const LastenradLandshutPage = () => {
  const headerColor = '#41678d'
  const { mockApp1, mockApp2, mockApp3 } = useStaticQuery(graphql`
    query {
      mockApp1: file(
        relativePath: { eq: "landing-page/mock-app-001-lastenrad.png" }
      ) {
        ...mockApp
      }
      mockApp2: file(
        relativePath: { eq: "landing-page/mock-app-002-lastenrad.png" }
      ) {
        ...mockApp
      }
      mockApp3: file(
        relativePath: { eq: "landing-page/mock-app-003-lastenrad.png" }
      ) {
        ...mockApp
      }
    }
  `)
  const usp =
    'Machen Sie es Ihren Bürgern:innen leicht Liegenschaften und Inventar auszuleihen.'

  return (
    <LandingPageLayout
      heroImage={lastenradLandshutHero}
      backgroundImage={lastenradLandshutBackground}
      usp={usp}
      sub="Eine digitale Buchungs Web App macht Schluss mit Excel Tabellen."
    >
      <Helmet>
        <meta name="twitter:card" content="summary" />
        <meta name="twitter:site" content="@compose_us" />
        <meta
          name="twitter:title"
          content="Landshut Lastenrad - Kommunen digital solutions"
        />
        <meta name="twitter:description" content={usp} />
        <meta name="twitter:image" content={lastenradLandshutHero} />
      </Helmet>
      <Container style={{ paddingTop: '4rem' }}>
        <LandingPageHeader label="FEATURES:" backgroundColor={headerColor} />
        <Box m={2}>
          <List aria-label="Features">
            <ListItem>
              <Hidden smDown>
                <ListItemIcon>
                  <ArrowForwardIosIcon
                    style={{ position: 'relative', top: -8, left: 0 }}
                  />
                </ListItemIcon>
              </Hidden>
              <ListItemText
                primary={
                  <Typography variant="h3" gutterBottom>
                    Mit unserer Web App folgen Ihre Bürger und Sie einem
                    einfachen Prozess.
                  </Typography>
                }
              />
            </ListItem>

            <ListItem>
              <Hidden smDown>
                <ListItemIcon>
                  <ArrowForwardIosIcon
                    style={{ position: 'relative', top: -8, left: 0 }}
                  />
                </ListItemIcon>
              </Hidden>
              <ListItemText
                primary={
                  <Typography variant="h3" gutterBottom>
                    Unsere Web App ist sicher und datenschutzkonform.
                  </Typography>
                }
              />
            </ListItem>

            <ListItem>
              <Hidden smDown>
                <ListItemIcon>
                  <ArrowForwardIosIcon
                    style={{ position: 'relative', top: -8, left: 0 }}
                  />
                </ListItemIcon>
              </Hidden>
              <ListItemText
                primary={
                  <Typography variant="h3" gutterBottom>
                    Unsere Web App ist mobil optimiert und bedienerfreundlich
                    gestaltet.
                  </Typography>
                }
              />
            </ListItem>
          </List>
        </Box>

        <LandingPageHeader
          label="IHRE VORTEILE:"
          backgroundColor={headerColor}
        />
        <Box m={2}>
          <Grid container spacing={6}>
            <Grid item xs={12} sm={6}>
              <Card variant="outlined" style={{ height: '100%' }}>
                <Img
                  fluid={mockApp1.childImageSharp.fluid}
                  style={{
                    height: 0,
                    paddingTop: '56.25%',
                  }}
                />
                <CardContent>
                  <Typography variant="h6">
                    Aus einem Kalender soll hervorgehen, wann und wie lange
                    etwas ausleihbar ist. Für jede Buchung wird eine
                    Buchungsbestätigung verschickt. Die Buchung ist nur mit
                    Registrierung möglich.
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
            <Grid item xs={12} sm={6}>
              <Card variant="outlined" style={{ height: '100%' }}>
                <Img
                  fluid={mockApp2.childImageSharp.fluid}
                  style={{
                    height: 0,
                    paddingTop: '56.25%',
                  }}
                />
                <CardContent>
                  <Typography variant="h6">
                    Die Reservierungen werden in einer online zugänglichen
                    Datenbank so lange wie nötig gespeichert. Das Hosting
                    übernimmt compose.us oder die Kommune selbst.
                  </Typography>
                </CardContent>
              </Card>
            </Grid>

            <Grid item xs={12}>
              <Card variant="outlined" style={{ height: '100%' }}>
                <Img
                  fluid={mockApp3.childImageSharp.fluid}
                  style={{
                    height: 0,
                    paddingTop: '56.25%',
                  }}
                />
                <CardContent>
                  <Typography variant="h6">
                    Die Web App ist eine Website, d.h. sie wird nirgendwo
                    heruntergeladen. Sie existiert online. Es werden nur Daten
                    abgefragt, die wirklich nötig sind und nur nach Freigabe
                    durch den Nutzer übertragen.
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Box>

        <MailchimpEmailCapture label="Interessiert?" />
      </Container>
    </LandingPageLayout>
  )
}

export default LastenradLandshutPage
